"use client";
import { useState } from 'react';

export default function GetKey() {
  const [key, setKey] = useState("");
  const [status, setStatus] = useState("idle"); // idle, loading, success, error

  const fetchKeyFromApi = async () => {
    setStatus("loading");
    try {
      const response = await fetch("https://api-kn3m.onrender.com/api/generate-key", {
        method: "POST",
        headers: { "Content-Type": "application/json" }
      });

      // CHECK IF RESPONSE IS HTML (Render Error/404)
      const contentType = response.headers.get("content-type");
      if (!contentType || !contentType.includes("application/json")) {
        console.error("Received non-JSON response from server.");
        throw new Error("API is currently waking up or offline. Please wait 30s and refresh.");
      }

      const data = await response.json();
      if (data.key) {
        setKey(data.key);
        setStatus("success");
      } else {
        throw new Error("API returned success but no key found.");
      }
    } catch (error: any) {
      alert(error.message);
      setStatus("error");
    }
  };

  return (
    <div className="min-h-screen bg-[#0F0F12] flex items-center justify-center font-sans text-white">
      <div className="bg-[#17191C] border border-[#2A2D36] p-8 rounded-2xl w-full max-w-md text-center shadow-2xl">
        <h1 className="text-3xl font-black text-[#8C5AFF] tracking-tight mb-2">OBLIVION X</h1>
        <p className="text-gray-400 text-sm mb-8">Access Gateway</p>

        {status !== "success" ? (
          <button 
            onClick={fetchKeyFromApi}
            disabled={status === "loading"}
            className="w-full bg-[#8C5AFF] hover:bg-[#7a49e6] py-3 rounded-lg font-bold transition-all disabled:opacity-50"
          >
            {status === "loading" ? "WAKING UP SERVER..." : "GET ACCESS KEY"}
          </button>
        ) : (
          <div className="space-y-4 animate-in fade-in zoom-in duration-300">
            <div className="bg-[#1E2026] p-4 rounded border border-[#8C5AFF]/30">
              <p className="text-xs text-gray-500 uppercase mb-2">Your Key</p>
              <code className="text-[#8C5AFF] text-lg font-mono tracking-widest">{key}</code>
            </div>
            <button 
              onClick={() => navigator.clipboard.writeText(key)}
              className="text-sm text-gray-400 hover:text-white transition-colors"
            >
              Copy to Clipboard
            </button>
          </div>
        )}
      </div>
    </div>
  );
}